@import "https://fonts.googleapis.com/css2?family=Barlow+Condensed&family=Bellefair&family=Nunito:wght@400;600;700&display=swap";
@import "https://cdn.jsdelivr.net/npm/@xz/fonts@1/serve/plus-jakarta-display.min.css";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: #fff;
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  height: 100vh;
  width: 100%;
  color: #fff;
  background: linear-gradient(to right, #313131, #191818);
  font-family: Nunito, sans-serif;
  font-size: 10px;
}

#app-name {
  text-align: center;
  font-size: 2rem;
}

